* {
    box-sizing: border-box;

}

html {
    line-height: 1.6;
    min-height: 100%;
    font-family: Roboto;

}

#root{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
    margin: 0;
}

img {
    width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0;
    margin-top: 0;
    font-weight: normal;
}

h1 {
    // font-size: rem(60px);
    margin-bottom: rem(20px);
}

p {
    // margin: 0 0 rem(12px);
    max-width: 60ch;
}

a{
    text-decoration: none;
}

button:hover{
  cursor: pointer;
}


.header {
    background-color: #fff;
    box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
    // position: fixed;
    height:40px;
    width: 100%;
    z-index: 3;
  }
  
  .header ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    // background-color: #fff;
  }
  
  .header li a {
    width:40px;
    height: 40px;
    display: block;
margin:0 10px 0;
     padding: 5px 5px;
    text-decoration: none;
  }
  

  .header .logo {
    display: block;
    float: left;
    font-size: 1em;
    // padding: 10px 20px;
    text-decoration: none;
  }
  
  /* menu */
  
  .header .menu {
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;
  }
  
  /* menu icon */
  
  .header .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
  }
  
  .header .menu-icon .navicon {
    background: #333;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
  }
  
  .header .menu-icon .navicon:before,
  .header .menu-icon .navicon:after {
    background: #333;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
  }
  
  .header .menu-icon .navicon:before {
    top: 5px;
  }
  
  .header .menu-icon .navicon:after {
    top: -5px;
  }
  
  /* menu btn */
  
  .header .menu-btn {
    display: none;
  }
  
  .header .menu-btn:checked ~ .menu {
    max-height: 240px;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
  }
  
  /* 48em = 768px */
  
  @media (min-width: 48em) {
    .header li {
      float: left;
    }
    .header li a {
      // padding: 20px 30px;
    }
    .header .menu {
      clear: none;
      float: right;
      max-height: none;
    }
    .header .menu-icon {
      display: none;
    }
  }
  
